<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" ref="filterFormRef" :model="params" class="demo-form-inline">
        <el-form-item clearable label="财务组织" size="mini" :rules="rules.company_id" prop="company_id">
          <el-select v-model="params.company_id" placeholder="请选择" @change="companyChange" :filter-method="companyChange" @focus="getCompany" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="审核日期" size="mini">
          <el-date-picker v-model="timeValue" @change="change" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="timeValue2" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="客户名称" size="mini">
          <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="业务员" size="mini">
          <el-select clearable v-model="params.employee_id" multiple collapse-tags @focus="EmployeeListInvok" filterable>
            {{(function(){if(params.employee_id == 0)params.employee_id = null})()}}
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch('filterFormRef')" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>

      </el-form>
    </div>
    <div class="add_delet" v-if="authority.b_export==1 && listShow">
      <el-dropdown placement="bottom-start">
        <span split-button style="cursor:pointer;color:#4e90e2">
          <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
          <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
          <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <!-- <div class="add_delet">
      <el-select v-model="params.export_type" placeholder="请选择">
        <el-option label="CSV逗号分隔" value="2">CSV逗号分隔</el-option>
        <el-option label="Excel文件" value="1">Excel文件</el-option>
      </el-select>
      <el-button plain size="mini" @click="exportFile" icon="iconfont icon-daochu10" class="exporT" v-if="authority.b_export==1">导出</el-button>
    </div> -->
    <div class="table_container" v-if="listShow">
      <!-- <h2 class="table_container_title">客户往来汇总表</h2> -->
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" :max-height="tableHeight">
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="财务组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="employee_name" label="业务员"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="early_stage" label="期初余额" align="right">
          <template slot-scope="scope">
            {{scope.row.early_stage|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receivable_price" label="应收款" align="right">
          <template slot-scope="scope">
            {{scope.row.receivable_price|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receipt_price" label="收款" align="right">
          <template slot-scope="scope">
            {{scope.row.receipt_price|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="terminal" label="期末余额" align="right">
          <template slot-scope="scope">
            {{scope.row.terminal|priceFormatNone}}
          </template>
        </el-table-column>
      </el-table>
      <div class="table_info">
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <div class="report_row">本页期末合计：{{TableData.allList?TableData.allList.perpage_terminal:0|priceFormatNone}}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr">期末总合计：{{all_terminal|priceFormatNone}}</div>
          </el-col>
        </el-row>
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <div>生成时间：{{TableData.allList?TableData.allList.time:""}}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr">制表人：{{TableData.allList?TableData.allList.name:""}}</div>
          </el-col>
        </el-row>
        <el-row type="flex" class="row-bg report_row">
          <el-col :span="12">
            <div>审核日期起始:{{this.params.start_time}}</div>
          </el-col>
          <el-col :span="12">
            <div class="textAr">审核日期截止:{{this.params.end_time}}</div>
          </el-col>
        </el-row>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import {
  getData,
  exportFile,
  getInventoryFinanceCompanyOpen,
  tableResize //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
export default {
  name: "customeAccountlog",
  data() {
    return {
      authority: {},
      params: {},
      all_terminal: "正在计算中...",
      timeValue2: [],
      searchParams: {},
      listShow: false,
      timeValue: [],
      exportType: null,
      TableData: {
        allList: {},
        tableData: []
      },
      companyList: [],
      goPage: 1, //默认搜索页面
      pageSize: 30,
      per_page: [30, 50, 100, 500],
      loading: false,
      tableHeight: 600
    };
  },

  methods: {
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams, 2);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams, 2);
    },
    companyChange() {
      this.params.employee_id = "";
    },
    exportOrder(scheduleType) {
      //导出
      let s = Object.assign({}, this.params, {
        export_type: scheduleType
      });
      let arg = {
        params: s,
        API: "exportCustomerContactsListUrl"
      };
      exportFile(arg);
    },
    change(val) {
      //选择的日期
      this.params.start_time = val[0];
      this.params.end_time = val[1];
    },
    onReset() {
      this.timeValue = [];
      this.timeValue2 = [];
      this.params = {};
      this.companyList = [
        {
          company_id: Number(localStorage.company_id),
          company_name: localStorage.company_name
        }
      ];
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id)
      });
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "getCustomerContactsList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams, 2, () => {
        this.$set(this, "all_terminal", "正在计算中...");
        this.$axios({
          method: "get",
          url: this.$api.getCustomerDealingsListv2,
          params: Object.assign({}, this.params)
        }).then(({ data }) => {
          if (data.code == 200) {
            this.$set(this, "all_terminal", data.data.desc.all_terminal);
          }
        });
      });
    },
    onSearch(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.listShow = true;
          if (this.timeValue && this.timeValue.length == 2) {
            this.params.start_time = this.timeValue[0];
            this.params.end_time = this.timeValue[1];
          } else {
            this.params.start_time = this.params.end_time = "";
          }
          if (this.timeValue2 && this.timeValue2.length == 2) {
            this.params.business_start_time = this.timeValue2[0];
            this.params.business_end_time = this.timeValue2[1];
          } else {
            this.params.business_start_time = this.params.business_end_time =
              "";
          }
          this.$set(this, "searchParams", {
            params: Object.assign({}, this.params, {
              page: 1
            }),
            TableData: this.TableData, //列表数据
            API: "getCustomerContactsList", //接口名
            vm: this //传递的this
          });
          getData(this.searchParams, 2, () => {
            this.$set(this, "all_terminal", "正在计算中...");
            this.$axios({
              method: "get",
              url: this.$api.getCustomerDealingsListv2,
              params: Object.assign({}, this.params)
            }).then(({ data }) => {
              if (data.code == 200) {
                this.$set(this, "all_terminal", data.data.desc.all_terminal);
              }
            });
          });
        }
      });
    },
    companyChange(val) {
      let options = {
        type: 2,
        company_name: val
      };
      this.$set(this.params, "employee_id", "");
      getInventoryFinanceCompanyOpen(this, options);
    },
    getCompany() {
      let options = {
        type: 2,
        company_name: null
      };
      getInventoryFinanceCompanyOpen(this, options);
    }
  },
  mounted() {
    this.companyList = [
      {
        company_id: Number(localStorage.company_id),
        company_name: localStorage.company_name
      }
    ];
    this.$set(this, "params", {
      company_id: Number(localStorage.company_id)
    });
    this.authority = JSON.parse(localStorage.getItem("authority"));
    //取消单据右侧滚动条

    tableResize.bind(this)();
    window.onresize = tableResize.bind(this);
  }
};
</script>
